import LayoutHOC from "layout/Layout";
import React, { useEffect, useState } from "react";

import { Layout, PageHeader } from "antd";
import { Content } from "antd/lib/layout/layout";

import { useUser } from "context/UserContext";
import { useNavigate, useParams } from "react-router-dom";

import { AnalyticsDashboard } from "react-analytics-charts";
// Over ten different commonly used charts are available
import { SessionsByDateChart, SessionsGeoChart } from "react-analytics-charts";

const AnalyticClient = (props) => {
    const { id } = useParams();
    const { logout, isLoggedIn, authData } = useUser();
    const navigate = useNavigate();
    useEffect(() => {
        if (
            !isLoggedIn ||
            (!authData?.super &&
                id.toString() !== authData?.projectId.toString())
        ) {
            logout();
            navigate("/login");
            return null;
        }
    }, [isLoggedIn, authData?.super, authData?.projectId]);

    return (
        <LayoutHOC>
            <Layout
                style={{
                    height: "100%",
                    display: "flex",
                    flex: 1,
                    background: "white",
                }}
            >
                <PageHeader
                    onBack={() => navigate(`/p-admin/${id}`)}
                    title={"Analytics"}
                    style={{ boxShadow: "1px 1px 10px 1px #ccc" }}
                />

                <Content
                    style={{
                        flex: 1,
                        overflow: "auto",
                        padding: "16px",
                        maxWidth: "800px",
                        width: "100%",
                        margin: "auto",
                    }}
                >
                    <AnalyticsDashboard
                        authOptions={{
                            clientId:
                                "123456789012-abc123def456ghi789jkl012mno345p.apps.googleusercontent.com",
                        }}
                        renderCharts={(gapi, viewId) => {
                            return (
                                <div>
                                    <SessionsByDateChart
                                        gapi={gapi}
                                        viewId={viewId}
                                        showPageViews
                                        showUsers
                                    />
                                    <SessionsGeoChart
                                        gapi={gapi}
                                        viewId={viewId}
                                        showPageViews
                                    />
                                    ... More charts here ...
                                </div>
                            );
                        }}
                    />
                </Content>
            </Layout>
        </LayoutHOC>
    );
};

export default AnalyticClient;
